/**
 * Styling for admin tools that are displayed on the frontend theme.
 */
/**
 * Custom variables that are not overrides of Foundation variables.
 */
.contextual-toolbar-tab.toolbar-tab {
  display: none !important; }

body.toolbar-fixed.toolbar-horizontal .header, body.toolbar-fixed.toolbar-vertical .header {
  margin-top: 40px; }

header.header .contextual,
.mobile-menu .contextual,
.secondary-menu .contextual,
.region--local-tasks-tabs .contextual {
  display: none !important; }

.contextual.open > button.trigger {
  display: block !important; }

.node .contextual .entitynodedelete-form {
  display: none !important; }

.node--view-mode--full > .contextual {
  right: 64px; }

.node:not(.node--view-mode--full) .contextual {
  right: 45px; }

.block > .contextual {
  right: 45px; }
  .block > .contextual .settings-trayblock-configure {
    display: none !important; }

.paragraph--type--container.width--full > .container__inner .container__content.width--full .paragraph--type--column.xlarge-12 .block > .contextual {
  right: 65px; }

.block-menu > .contextual {
  right: 45px; }
  .block-menu > .contextual .settings-trayblock-configure {
    display: none !important; }

.paragraph .contextual[data-contextual-id^="paragraph:"] {
  display: none !important; }
  .paragraph .contextual[data-contextual-id^="paragraph:"] .quickedit,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-editdelete-form,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-frontend-uiedit-settings,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-frontend-uiduplicate,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-frontend-uimove-up,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-frontend-uimove-down,
  .paragraph .contextual[data-contextual-id^="paragraph:"] .paragraphs-frontend-uiadd {
    display: none !important; }

.paragraph--type--container {
  position: relative; }

.paragraph--type--container > .contextual[data-contextual-id^="paragraph:"],
.paragraph--type--container > .container__inner > .container__content > .grid-x > .contextual[data-contextual-id^="paragraph:"] {
  display: block !important;
  top: 7px; }
  .paragraph--type--container > .contextual[data-contextual-id^="paragraph:"] .trigger,
  .paragraph--type--container > .contextual[data-contextual-id^="paragraph:"] .contextual-links,
  .paragraph--type--container > .container__inner > .container__content > .grid-x > .contextual[data-contextual-id^="paragraph:"] .trigger,
  .paragraph--type--container > .container__inner > .container__content > .grid-x > .contextual[data-contextual-id^="paragraph:"] .contextual-links {
    border-color: #E77200;
    border-width: 2px; }

.paragraph--type--column {
  position: relative; }
  .paragraph--type--column > .contextual[data-contextual-id^="paragraph:"] {
    display: block !important;
    right: 12px; }
    .paragraph--type--column > .contextual[data-contextual-id^="paragraph:"] .trigger,
    .paragraph--type--column > .contextual[data-contextual-id^="paragraph:"] .contextual-links {
      border-color: #E77200; }
  .paragraph--type--container.width--full > .container__inner .container__content.width--full .paragraph--type--column > .contextual[data-contextual-id^="paragraph:"] {
    right: 32px; }
  .paragraph--type--column .node .contextual {
    display: block !important; }

.node--full__sidebar .paragraph {
  position: relative; }
  .node--full__sidebar .paragraph > .contextual[data-contextual-id^="paragraph:"] {
    display: block !important; }
    .node--full__sidebar .paragraph > .contextual[data-contextual-id^="paragraph:"] .trigger,
    .node--full__sidebar .paragraph > .contextual[data-contextual-id^="paragraph:"] .contextual-links {
      border-color: #E77200; }

.ui-widget.ui-front form label,
.ui-widget.ui-front form legend {
  margin-bottom: 0;
  font-weight: bold; }

.ui-widget.ui-front form tr.odd .form-item,
.ui-widget.ui-front form tr.even .form-item {
  margin: 0 0 20px 0; }

.ui-widget.ui-front form .form-item {
  margin: 0 0 20px 0; }

.ui-widget.ui-front form .label {
  font-family: "Ideal Sans A", "Ideal Sans B", "Helvetica", "Arial", sans-serif;
  color: #000000;
  background-color: transparent; }

.ui-widget.ui-front form .form-item .description {
  margin-top: 5px; }

.ui-widget.ui-front form .button,
.ui-widget.ui-front form button {
  margin: 0;
  font-size: 13px;
  font-weight: normal;
  padding: 7px 10px;
  border-radius: 4px; }

.ui-widget.ui-front form [type='text'],
.ui-widget.ui-front form [type='password'],
.ui-widget.ui-front form [type='date'],
.ui-widget.ui-front form [type='datetime'],
.ui-widget.ui-front form [type='datetime-local'],
.ui-widget.ui-front form [type='month'],
.ui-widget.ui-front form [type='week'],
.ui-widget.ui-front form [type='email'],
.ui-widget.ui-front form [type='number'],
.ui-widget.ui-front form [type='search'],
.ui-widget.ui-front form [type='tel'],
.ui-widget.ui-front form [type='time'],
.ui-widget.ui-front form [type='url'],
.ui-widget.ui-front form [type='color'] {
  margin-bottom: 0;
  box-shadow: none;
  border-color: #CCCCCC; }

.ui-widget.ui-front form .field--widget-string-textarea {
  margin-bottom: 20px; }

.ui-widget.ui-front form .form-item.form-type-textarea,
.ui-widget.ui-front form tr.odd .form-item.form-type-textarea,
.ui-widget.ui-front form tr.even .form-item.form-type-textarea {
  margin-bottom: 0; }

.ui-widget.ui-front form select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  position: relative;
  margin-bottom: 0;
  border-radius: 0;
  border-color: #CCCCCC; }

.ui-widget.ui-front form input[type="checkbox"],
.ui-widget.ui-front form input[type="radio"] {
  margin: 0; }

.ui-widget.ui-front form [type="checkbox"] + label,
.ui-widget.ui-front form [type="radio"] + label {
  margin: 0 11px;
  vertical-align: bottom; }

.ui-widget.ui-front form .form-type-radio .description,
.ui-widget.ui-front form .form-type-checkbox .description {
  margin-top: 0;
  margin-left: 29px; }

.ui-widget.ui-front form input[type="checkbox"]:after {
  border-color: #CCCCCC; }

.ui-widget.ui-front form .field-multiple-table .draggable > td {
  padding: 10px; }

.ui-widget.ui-front form .field-multiple-table .draggable .field-multiple-drag {
  padding: 0; }

.ui-widget.ui-front form .field-multiple-table .draggable a.tabledrag-handle {
  height: 100%;
  margin-left: 0;
  padding: 13px 10px !important; }

.ui-widget.ui-front form .field-multiple-table .draggable a.tabledrag-handle .handle {
  width: 22px;
  height: 22px;
  margin: 0;
  padding: 0;
  background-size: contain;
  background-position: center; }

.ui-widget.ui-front form .field-multiple-table .draggable > td > .form-item:first-child:last-child {
  margin-bottom: 0; }

.ui-widget.ui-front form .dropbutton .dropbutton-action .button {
  display: block;
  width: 100%;
  margin: 0;
  padding: 7px 15px;
  color: #4E5149;
  background-color: transparent;
  border-color: transparent;
  border-radius: 0; }
  .ui-widget.ui-front form .dropbutton .dropbutton-action .button:hover, .ui-widget.ui-front form .dropbutton .dropbutton-action .button:focus {
    background-color: #EEE;
    border-color: transparent; }

.ui-widget.ui-front form .paragraphs-dropdown-actions .button {
  margin: 0;
  padding: 7px 15px;
  font-size: 13px;
  color: #4E5149;
  background-color: transparent;
  border-color: transparent;
  border-radius: 0; }
  .ui-widget.ui-front form .paragraphs-dropdown-actions .button:hover, .ui-widget.ui-front form .paragraphs-dropdown-actions .button:focus {
    background-color: #FFFFFF;
    border-color: transparent; }

.ui-widget.ui-front form .paragraph-type-title {
  flex-basis: auto; }

.ui-widget.ui-front form .form-submit,
.ui-widget.ui-front .form-submit {
  margin: 10px 0;
  font-size: 13px;
  font-weight: normal;
  padding: 7px 10px;
  border-radius: 4px;
  text-transform: uppercase; }

.field--widget-text-textarea .cke_contents {
  padding: 10px;
  background-color: #F5F5F5; }
